.roadForWhomWrapper {
  display: flex;
  padding-top: 78px;
  justify-content: space-between;
  padding-bottom: 68px;
  position: relative;
  overflow: hidden;
}
.roadForWhomFooter {
  background-color: var(--black);
  width: 100%;
  text-transform: uppercase;
  text-align: center;
  color: var(--orange);
  padding-bottom: 24px;
  padding-top: 24px;
  position: relative;
  z-index: 1;
}
.roadForWhomFooter > span {
  /* TODO add font size */
  font-size: 48px;
  font-family: var(--fontFamilyMontserrat);
  /* TODO add font weight */
  font-weight: 900;
}
.roadForWhomWrapper::before,
.rightSide::before {
  content: '';
  display: block;
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translate(-50%, 0);
  height: 100%;
  width: 25%;
  background-image: url(../../images/road/to.png);
  background-size: contain;
  background-repeat: no-repeat;
}
.rightSide::before {
  display: none;
}
.rightSide,
.leftSide,
.photoContainer {
  width: 100%;
  flex-basis: 33.3%;
}
.imgWrapperMob {
  position: relative;
}

.list {
  padding-left: 50px;
}
.list > li {
  margin-bottom: 15px;
  position: relative;
  color: var(--black);
  font-size: var(--fontSize-xs);
  font-weight: var(--fontWeight-s);
  font-family: var(--fontFamilyMontserrat);
}
.list > li::after {
  content: ' ';
  display: block;
  width: 12px;
  height: 12px;
  background-color: var(--black);
  left: -25px;
  position: absolute;
  top: 8px;
}

.subtitle {
  color: var(--grey);
  font-size: var(--fontSize-xl);
  text-transform: uppercase;
  font-family: 'Bebas';
}
.happyness {
  margin-bottom: 110px;
  display: block;
  margin-left: auto;
  width: fit-content;
  text-transform: uppercase;
}
.btn {
  background-color: var(--orange);
  text-transform: uppercase;
  display: inline-block;
  margin-top: 78px;
}
.photoContainer {
  position: relative;
  z-index: 1;
  height: auto;
}
.photoContainer::after {
  content: '';
  display: block;
  position: absolute;
  bottom: -98px;
  right: -110px;
  width: 98px;
  height: 98px;
  border-radius: 50%;
  background-color: var(--orange);
}
.photo {
  position: absolute;
  height: 100%;
  bottom: -100px;
  z-index: 1;
}

.title {
  font-family: var(--fontFamilyBebas);
  font-size: var(--fontSize-xl);
  white-space: nowrap;
  text-transform: uppercase;
  font-family: var(--fontFamilyBebas);
}
.description {
  display: block;
  font-size: var(--fontSize-s);
  font-weight: var(--fontWeight-s);
  font-family: var(--fontFamilyMontserrat);
}
.toGetTitle {
  display: block;
  margin-top: 20px;
  font-family: var(--fontFamilyMontserrat);
  font-size: var(--fontSize-s);
  font-weight: var(--fontWeight-s);
}
.name {
  font-family: var(--fontFamilyMontserrat);
  font-size: var(--fontSize-s);
  font-weight: var(--fontWeight-m);
  white-space: nowrap;
}

@media screen and (width <= 1300px) {
  .photo {
    height: 90%;
    z-index: 1;
  }

  .list li {
    font-size: clamp(var(--fontSize-smaller), 2vw, var(--fontSize-xs));
  }

  .happyness {
    margin-bottom: 80px;
  }
}

@media screen and (width <= 1240px) {
  .rightSide::before {
    left: 0;
  }
  .roadForWhomWrapper {
    padding-top: 66px;
  }
  .roadForWhomWrapper::before {
    display: none;
  }

  .rightSide {
    position: relative;
  }
  .rightSide::before {
    display: block;
    left: 0;
    top: 0;
    width: 75px;
  }
  .photoContainer {
    flex-basis: 10%;
  }
  .leftSide,
  .rightSide {
    flex-basis: 45%;
  }
  .photo {
    height: 70%;
    left: 0;
  }
  .btn {
    position: relative;
    z-index: 2;
    opacity: 1;
  }
  .description,
  .toGetTitle,
  .name {
    font-size: 20px;
  }
  .subtitle,
  .title {
    font-size: var(--fontSize-l);
  }
  .list li {
    font-size: 20px;
  }
  .photo {
    transform: translate(-50%, 0);
    left: 50%;
  }
  .happyness {
    margin-bottom: 20px;
    margin-left: 70px;
  }
  .name {
    white-space: wrap;
    padding-right: 5%;
  }
}

@media screen and (width <= 950px) {
  .roadForWhomWrapper::before {
    width: 100px;
    left: 20px;
  }
  .photo {
    height: 60%;
  }

  .photoContainer::after {
    right: -300px;
  }
}

@media screen and (width <= 900px) and (orientation: portrait) {
  .photoContainer {
    display: none;
  }
}

@media screen and (width < 768px) {
  .roadForWhomWrapper {
    flex-direction: column;
    padding-top: 66px;
    padding-left: 0;
    padding-right: 0;
  }
  .leftSide {
    padding-left: 25px;
    padding-right: 25px;
    padding-bottom: 25px;
  }
  .description,
  .name {
    font-size: var(--fontSize-xxs);
  }
  .rightSide::before {
    display: none;
  }
  .toGetTitle {
    margin-top: 40px;
    font-size: var(--fontSize-xxs);
  }
  .photoContainer {
    width: 100%;
    position: relative;
  }
  .photo {
    width: 100%;
    height: auto;
    position: static;
    left: 0;
  }
  .list {
    padding-right: 25px;
    padding-left: 40px;
  }
  .list li::after {
    left: -15px;
    width: 8px;
    height: 8px;
  }
  .titleMob {
    margin-top: 60px;
    margin-bottom: 12px;
    padding-left: 25px;
    padding-right: 25px;
  }
  .roadForWhomFooter {
    background-color: transparent;
    max-width: 630px;
    margin: 0 auto;
    width: 100%;
    margin: 0 auto;
    text-transform: uppercase;
    text-align: center;
    padding-bottom: 24px;
    padding-top: 0;
    position: relative;
    z-index: 1;
  }
  .roadForWhomFooter > span {
    /* TODO add font size */
    font-size: 30px;
    color: var(--orange);
    font-family: var(--fontFamilyMontserrat);
    /* TODO add font weight */
    font-weight: 900;
  }
  .titleMob {
    margin-top: 40px;
  }
}

@media screen and (width <= 480px) {
  .description,
  .toGetTitle {
    font-size: 22px;
  }
  .name {
    padding-right: 0;
  }
}

@media screen and (width <= 1240px) and (orientation: landscape) {
  .btn {
    margin-top: 25px;
  }
  .photoContainer {
    flex-basis: 10%;
  }
  .leftSide,
  .rightSide {
    flex-basis: 45%;
  }

  .rightSide::before {
    left: 0;
  }

  .name {
    white-space: wrap;
    padding-right: 40px;
  }
}
@media screen and (width < 768px) and (orientation: landscape) {
  .photo {
    display: none;
  }
  .shadow {
    display: none;
  }
}

@media screen and (width <= 768px) {
  .description,
  .toGetTitle,
  .name {
    font-size: 18px;
  }
  .toGetTitle {
    margin-top: 20px;
  }
  .btn {
    margin-top: 40px;
  }
  .roadForWhomFooter {
    padding-left: 10px;
    padding-right: 10px;
  }
}
@media screen and (width < 768px) and (orientation: portrait) {
  .photo {
    transform: translate(0, 0);
  }
}
@media screen and (width <= 480px) and (orientation: portrait) {
  .photoContainer {
    display: block;
  }
}
